<template>
    <div>
        <ConsoleHeader />
      
        <div class="content-wrapper">
            <!-- Content Header (Page header) -->
            <div class="content-header">
                <div class="container-fluid">
                    <div class="row mb-2">
                    <div class="col-sm-6 text-start">
                        <h1 class="m-0 text-dark">Dashboard</h1>
                    </div><!-- /.col -->
                    <div class="col-sm-6">
                        <ol class="breadcrumb float-sm-right">
                        <li class="breadcrumb-item"><router-link to="/console" >Home</router-link></li>
                        <li class="breadcrumb-item active">Dashboard</li>
                        </ol>
                    </div><!-- /.col -->
                    </div><!-- /.row -->
                </div><!-- /.container-fluid -->
            </div>
            <!-- /.content-header -->

            <!-- Main content -->
            <section class="content">
                <div class="container-fluid" >
                    <!-- Small boxes (Stat box) -->
                    <VueSlickCarousel :arrows="true" :dots="true" v-bind="settings">
                        
                        <!-- small box -->
                        <div class="me-2">
                            <div class="small-box bg-green">
                                <div class="inner ">
                                    <h3>{{$store.getters.users.filter(user => user.role === 'member').length}}</h3>

                                    <p>Members</p>
                                </div>
                                <div class="icon">
                                    <i class="ion ion-bag"></i>
                                </div>
                                <router-link to="/console/users" class="small-box-footer p-2">More info <i class="fas fa-arrow-circle-right"></i></router-link>
                            </div>

                        </div>
                        
                        <!-- small box -->
                        <div class="me-2">
                            <div class="small-box bg-dark">
                                <div class="inner">
                                <h3>{{units.length}}</h3>

                                <p>Units</p>
                                </div>
                                <div class="icon">
                                    <i class="ion ion-bag"></i>
                                </div>
                                <router-link to="/console/units" class="small-box-footer p-2">More info <i class="fas fa-arrow-circle-right"></i></router-link>
                            </div>
                        </div>
                        
                        <!-- small box -->
                        <div class="me-2">
                            <div class="small-box bg-info">
                                <div class="inner">
                                <h3>{{events.length}}</h3>

                                <p>Events</p>
                                </div>
                                <div class="icon">
                                    <i class="ion ion-bag"></i>
                                </div>
                                <router-link to="/console/events" class="small-box-footer p-2">More info <i class="fas fa-arrow-circle-right"></i></router-link>
                            </div>
                        </div>
                        
                        <!-- small box -->
                        <div class="me-2">
                            <div class="small-box bg-danger">
                                <div class="inner">
                                <h3>{{$store.getters.users.filter(user => user.role === 'admin' && user.id !== 1).length}}</h3>

                                <p>Executives</p>
                                </div>
                                <div class="icon">
                                <i class="ion ion-bag"></i>
                                </div>
                                <router-link to="/console/executives" class="small-box-footer p-2">More info <i class="fas fa-arrow-circle-right"></i></router-link>
                            </div>
                        </div>
                        
                    </VueSlickCarousel>
            
                </div>

            </section>
            <!-- /.content -->
        </div>

        <ConsoleFooter />
    </div>
</template>


<script>
import ConsoleHeader from "./layouts/ConsoleHeader.vue";
import ConsoleFooter from "./layouts/ConsoleFooter.vue";

import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
// optional style for arrows & dots
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
import { mapGetters, mapActions } from 'vuex';

export default {
    name: "ConsoleIndex",
    data(){
        return {
            settings: {
                "dots": true,
                "infinite": false,
                "speed": 500,
                "slidesToShow": 4,
                "slidesToScroll": 4,
                "initialSlide": 0,
                "responsive": [
                {
                    "breakpoint": 1024,
                    "settings": {
                    "slidesToShow": 3,
                    "slidesToScroll": 3,
                    "infinite": true,
                    "dots": true
                    }
                },
                {
                    "breakpoint": 600,
                    "settings": {
                    "slidesToShow": 2,
                    "slidesToScroll": 2,
                    "initialSlide": 2
                    }
                },
                {
                    "breakpoint": 480,
                    "settings": {
                    "slidesToShow": 1,
                    "slidesToScroll": 1
                    }
                }
                ]
            }
        }
    },
    computed: mapGetters(['user', 'units', 'events']),
    components: {
        ConsoleHeader,
        ConsoleFooter,
        VueSlickCarousel
    },
    methods: {
        ...mapActions(['fetchUnits', 'fetchEvents']),
    },
    created(){
        this.fetchUnits();
        this.fetchEvents();
    }

}
</script>

<style scoped>
    .small-box{
        margin-right: 1em;
    }
</style>